// Portfolio search bar.
.filter--search____searchbar {
  display: inline-block;
  float: right;
  height: 40px;
  margin: -70px 0 0;
  padding: 0;
  position: relative;

  img {
    margin: 6px 6px 0 0;
  }

  input[type="text"] {
    background: none;
    border: 0;
    color: $white;
    cursor: pointer;
    display: inline-block;
    height: 40px;
    outline: none;
    padding: 3px;
    padding-right: 60px;
    position: absolute;
    right: 0;
    top: 0;
    transition: width .4s cubic-bezier(.000, .795, .000, 1);
    width: 0;
    z-index: 3;

    &:focus {
      border-bottom: 1px solid $white;
      cursor: text;
      width: 250px;
      z-index: 1;
    }

    &:focus:hover {
      border-bottom: 1px solid $white;
    }
  }

  input[type="submit"] {
    background: url(''); // NOTE: Removing this breaks the search bar.
    border: 0;
    cursor: pointer;
    display: inline-block;
    float: right;
    height: 45px;
    opacity: .4;
    position: absolute;
    right: 0;
    text-indent: -10000px;
    top: 0;
    transition: opacity .4s ease;
    width: 45px;
    z-index: 2;

    &:hover {
      opacity: .8;
    }
  }
}
