h1,
ul {
  color: #fff;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
}

h2,
h4 {
  color: #fff;
  font-family: "Lora Italic", "Times New Roman", Times, serif;
  font-style: italic;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 18px;
}

p {
  color: #fff;
  font-family: "Roboto light", Arial, Helvetica, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.text--bold {
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
}

@keyframes logoGlow {
  from {
    border-radius: 50%;
    box-shadow: 0 0 19px 10px transparent, inset 0 0 19px 10px transparent;
  }
  50% {
    border-radius: 50%;
    box-shadow: 0 0 18px 10px #009ea8, inset 0 0 18px 10px #009ea8;
  }
  to {
    border-radius: 50%;
    box-shadow: 0 0 19px 10px transparent, inset 0 0 19px 10px transparent;
  }
}

@keyframes logoPulse {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}

@keyframes bouncingArrow {
  from {
    top: 50%;
  }
  20% {
    top: 30%;
  }
  40% {
    top: 50%;
  }
  60% {
    top: 40%;
  }
  80% {
    top: 50%;
  }
  to {
    top: 50%;
  }
}

.button {
  background: transparent;
  border: 3px solid #fff;
  color: #fff;
  cursor: pointer;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  padding: 10px;
  text-transform: uppercase;
  transition: .5s ease;
}

.button:hover {
  border: 3px solid #009ea8;
  overflow: visible;
  transform: scale(1.05);
}

.button__submit {
  padding: 2%;
  width: 100%;
}

.navbar {
  background: linear-gradient(to top, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5), rgba(255, 255, 255, 0));
  bottom: 0;
  height: 100px;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.navbar ul {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 10px 0;
  width: 100%;
}

.navbar li {
  display: inline-block;
  padding: 5px;
  position: relative;
  text-shadow: 1px 1px 1px #111;
}

.navbar li:first-child::after, .navbar li:last-child::after {
  background: transparent;
  content: '';
  display: block;
  height: 2px;
  margin: auto;
  margin-top: 3px;
  transition: width .5s ease, background-color .5s ease;
  width: 0;
}

.navbar li:nth-child(2) {
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-name: logoPulse;
}

.navbar li:nth-child(2)::before {
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-name: logoGlow;
  content: '';
  display: block;
  height: 100%;
  opacity: .5;
  pointer-events: none;
  position: absolute;
  transform: translate(-7%, -10%);
  width: 100%;
}

.navbar li:hover::after {
  background: #009ea8;
  width: 100%;
}

.navbar a {
  color: #fff;
  cursor: pointer;
  text-decoration: none;
}

.navbar--fixed-top {
  position: fixed;
  top: 0;
}

.filter--search____searchbar {
  display: inline-block;
  float: right;
  height: 40px;
  margin: -70px 0 0;
  padding: 0;
  position: relative;
}

.filter--search____searchbar img {
  margin: 6px 6px 0 0;
}

.filter--search____searchbar input[type="text"] {
  background: none;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  height: 40px;
  outline: none;
  padding: 3px;
  padding-right: 60px;
  position: absolute;
  right: 0;
  top: 0;
  transition: width 0.4s cubic-bezier(0, 0.795, 0, 1);
  width: 0;
  z-index: 3;
}

.filter--search____searchbar input[type="text"]:focus {
  border-bottom: 1px solid #fff;
  cursor: text;
  width: 250px;
  z-index: 1;
}

.filter--search____searchbar input[type="text"]:focus:hover {
  border-bottom: 1px solid #fff;
}

.filter--search____searchbar input[type="submit"] {
  background: url("");
  border: 0;
  cursor: pointer;
  display: inline-block;
  float: right;
  height: 45px;
  opacity: .4;
  position: absolute;
  right: 0;
  text-indent: -10000px;
  top: 0;
  transition: opacity .4s ease;
  width: 45px;
  z-index: 2;
}

.filter--search____searchbar input[type="submit"]:hover {
  opacity: .8;
}

input,
textarea {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  margin-bottom: 20px;
  padding: 2%;
  text-align: right;
  text-transform: uppercase;
  transition: .5s ease;
  width: 96%;
}

@media only screen and (max-width: 767px) {
  input,
  textarea {
    text-align: left;
  }
}

input:focus,
textarea:focus {
  border: 2px solid #009ea8;
  outline: none;
  overflow: visible;
  transform: scale(1.05);
}

@media only screen and (max-width: 767px) {
  #form {
    margin-bottom: 30px;
  }
}

.form-submission--success,
.form-submission--fail {
  display: none;
}

.form-submission--success p,
.form-submission--fail p {
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  font-size: 5vw;
  margin-top: 80px;
  text-align: center;
}

.container {
  margin: 0 auto;
  width: 90%;
  max-width: 1280px;
}

.container--small {
  width: 80%;
}

.index {
  overflow: hidden;
}

.landing-background {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../images/landing-background.png") no-repeat center center;
  background-size: cover;
  height: 100vh;
}

.landing-content {
  left: 50%;
  position: relative;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.landing-content h1 {
  font-size: 100px;
  margin-bottom: 5px;
  text-shadow: 1px 1px 2px #111;
}

.landing-content h2 {
  font-size: 40px;
  text-shadow: 1px 1px 2px #111;
}

.portfolio-background {
  height: 100vh;
}

.portfolio-content {
  height: calc(100vh - 100px);
  left: 0;
  position: absolute;
  text-shadow: 0.5px 0.5px 0.5px #111;
  top: 100vh;
  width: 100%;
}

.portfolio-content a {
  color: #fff;
}

.portfolio-content--fixed-top {
  position: fixed;
  top: 12vh;
}

.portfolio-content--fixed-top-transition {
  top: -100vh;
  transition: .750s;
}

.portfolio-content--left {
  float: left;
  height: 100%;
  margin-left: 5%;
  width: 45%;
}

.portfolio-content--left ul {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
}

.portfolio-content--left li {
  cursor: pointer;
}

.portfolio-content--left li h3 {
  text-transform: uppercase;
  transition: .750s;
}

.portfolio-content--left li h4 {
  margin-top: 5px;
}

.portfolio-content--right {
  float: right;
  height: 100%;
  margin-right: 5%;
  width: 45%;
}

.project-title__active {
  font-size: 28px;
}

.project--1__subtitle,
.project--2__subtitle,
.project--3__subtitle,
.project--4__subtitle,
.project--5__subtitle {
  font-size: 24px;
  margin-left: -200px;
  opacity: 0;
  transition: .750s;
}

.project--1__description,
.project--2__description,
.project--3__description,
.project--4__description,
.project--5__description {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: opacity .75s ease-out;
}

.project--1__description p,
.project--2__description p,
.project--3__description p,
.project--4__description p,
.project--5__description p {
  font-size: 20px;
}

.project-title--view-all {
  font-size: 22px;
  text-transform: uppercase;
}

.project-description {
  left: 50%;
  position: relative;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.project-description p {
  padding-bottom: 20px;
}

.portfolio-grid {
  column-count: 4;
  margin: 130px 0 20px;
}

.portfolio-grid img {
  height: auto;
  width: 100%;
}

.portfolio-grid-item__container {
  margin-bottom: 1em;
  text-align: center;
}

.portfolio-grid-item {
  line-height: 0;
  margin-bottom: 20px;
  position: relative;
  transition: .250s;
}

.portfolio-grid-item:hover {
  transform: scale(1.03);
}

.portfolio-grid-item__overlay {
  align-items: left;
  background: rgba(0, 0, 0, 0.5);
  border-bottom: 3px solid #009ea8;
  bottom: 0;
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 60px;
  justify-content: center;
  left: 0;
  opacity: 1;
  position: absolute;
  right: 0;
}

.portfolio-grid-item__overlay__title {
  font-size: 1.2rem;
  margin: 0 0 0 5%;
  text-transform: uppercase;
}

.portfolio-grid-item__overlay__subtitle {
  margin: 20px 0 0 5%;
}

@media (max-width: 1400px) {
  .portfolio-grid {
    column-count: 3;
  }
}

@media (max-width: 860px) {
  .portfolio-grid {
    column-count: 2;
  }
}

@media (max-width: 500px) {
  .portfolio-grid {
    column-count: 1;
  }
}

.about {
  margin-bottom: 40px;
}

.about-background {
  background: #111;
  min-height: 100vh;
}

.about-content {
  position: relative;
  top: 15vh;
}

.about-content--left {
  float: left;
  height: 100%;
  margin-right: 5%;
  width: 45%;
}

@media only screen and (max-width: 767px) {
  .about-content--left {
    float: none;
    margin-right: 0;
    width: 100%;
  }
}

.about-content--left img {
  display: block;
  margin: auto;
  padding: 0 0 5%;
}

.about-content--left p {
  margin-bottom: 5%;
}

.about-content--right {
  float: right;
  height: 100%;
  margin-left: 5%;
  text-align: right;
  width: 45%;
}

@media only screen and (max-width: 767px) {
  .about-content--right {
    float: none;
    margin-left: 0;
    text-align: left;
    width: 100%;
  }
}

.about-content--right h1 {
  font-size: 5vw;
  margin-top: 20px;
}

@media only screen and (max-width: 767px) {
  .about-content--right h1 {
    font-size: 38px;
    white-space: nowrap;
  }
}

.about__icons {
  display: flex;
  justify-content: flex-end;
  margin: 30px 0;
}

@media only screen and (max-width: 767px) {
  .about__icons {
    justify-content: flex-start;
  }
}

.about__icons path {
  transition: .5s;
}

.about__icons a {
  margin-left: 30px;
}

.about__icons a:hover path, .about__icons a:focus path, .about__icons a:active path {
  fill: #009ea8;
}

@media only screen and (max-width: 767px) {
  .about__icons a {
    margin-left: 0;
    margin-right: 30px;
  }
}

.about__icons object {
  pointer-events: none;
}

.heading {
  margin-top: 100px;
}

.heading::before {
  background: #009ea8;
  box-shadow: 1px 1px 2px #111;
  content: '';
  display: inline-block;
  height: 400px;
  margin: -300px 0 0 -200px;
  position: absolute;
  transform: rotate(45deg);
  width: 400px;
  z-index: 2;
}

.heading-background {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../images/landing-background.png") no-repeat center center;
  background-size: cover;
  height: auto;
}

.heading-content {
  padding: 175px 0;
  text-align: left;
  width: 100%;
}

.heading-content h1 {
  font-size: 5vw;
  margin-bottom: 5px;
  text-shadow: 1px 1px 2px #111;
}

.heading-content h2 {
  font-size: 3vw;
  text-shadow: 1px 1px 2px #111;
}

.intro p {
  font-size: 20px;
  padding: 80px 0;
  text-align: center;
}

.divider__circle--white {
  background: #fff;
  border-radius: 50%;
  color: #009ea8;
  content: '';
  display: inline-block;
  height: 100px;
  left: 50%;
  position: relative;
  transform: translateX(-50%);
  width: 100px;
}

.divider__circle--white::after {
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  animation-name: bouncingArrow;
  color: #009ea8;
  content: '▽';
  display: inline-block;
  font-size: 25px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.portfolio-options {
  display: flex;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  justify-content: flex-start;
  margin: 30px 0;
}

.portfolio-options svg {
  margin-bottom: -8px;
}

.portfolio-options path {
  transition: .5s;
}

.portfolio-options a {
  color: #111;
  margin-right: 30px;
  transition: .5s;
}

.portfolio-options a:hover, .portfolio-options a:focus, .portfolio-options a:active {
  color: #009ea8;
}

.portfolio-options a:hover path, .portfolio-options a:focus path, .portfolio-options a:active path {
  fill: #009ea8;
}

.main-body {
  background: #fff;
  margin-top: -50px;
  padding: 80px 0;
}

.main-body p {
  color: #111;
  font-size: 18px;
  margin-bottom: 30px;
}

.dropcap {
  color: #009ea8;
  float: left;
  font-family: "Roboto Black", Arial, Helvetica, sans-serif;
  font-size: 100px;
  line-height: 40px;
  margin-top: 20px;
  padding-bottom: 7px;
  padding-right: 7px;
}
